* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    user-select: none;
  }
  
  body {
    background-color: rgb(31, 31, 31);
  }
  
  button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 4rem;
    width: 10rem;
    font-size: 1.5rem;
    border-radius: 5px;
    border: none;
    box-shadow: 1px 1px 5px black;
    background-color: white;
  }

  h1 {
    color: white;
    text-align: center;
    padding-top: 5%;
    font-size: 4rem;
  }
  h2 {
    color: white;
    text-align: center;
    font-size: 2rem;
  }
  